ï»¿@device-break-width: 800px;
@bg-light: #dedede;
@bg-dark: #333;

body
{
    background-color: #ffffff;
    margin: 0;
    padding: 0;
}

.container
{
    display: flex;
    min-height: 100vh;
    flex-direction: column;
}

header {
    background-color: @bg-light;
    background-position: 20px center;
    background-repeat: no-repeat;
    background-image: url('data:image/png;base64,');
    height: 144px;
    padding: 20px;

    #toggleMenu
    {
        padding: 0;
        border: 0;
        float: right;
        background-color: transparent;
        display: none;

        @media all and (max-width: @device-break-width)
        {
            display: inline-block;
        }

        &:focus
        {
            outline: 0;
        }

        &.open span
        {
            opacity: 1;
            transform: rotate(45deg) translate(-21px, -19px);
            background: #232323;
        }

        &.open span:nth-last-child(3)
        {
            opacity: 0;
            transform: rotate(0deg) scale(0.2, 0.2);
        }

        &.open span:nth-last-child(2)
        {
            opacity: 1;
            transform: rotate(-45deg) translate(0, -1px);
        }

        span
        {
            display: block;
            width: 33px;
            height: 4px;
            margin-bottom: 5px;
            position: relative;
  
            background: @bg-dark;
            border-radius: 3px;
  
            z-index: 1;
  
            transform-origin: 4px 0px;
  
            transition: transform 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        background 0.5s cubic-bezier(0.77,0.2,0.05,1.0),
                        opacity 0.55s ease;

            &:first-child
            {
                transform-origin: 0% 0%;
            }
            &:nth-last-child(2)
            {
                transform-origin: 0% 100%;
            }
        }
    }
}

main
{
    display: flex;
    flex: 1;
}

nav
{
    flex: 1;

    @media all and (max-width: @device-break-width)
    {
        display: none;
    }
}

article
{
    flex: 3;
}